<template>
  <el-table-column prop="" :width="getWidth(columnEl)" :fixed="getFixed(columnEl)" :align="getAlign(columnEl)" :type="getType(columnEl)" :reserve-selection="true">
    <template v-if="columnEl.checkHeader" slot="header">
      <el-checkbox :disabled="columnEl.headerDisabled" :v-model="colCheckHeader" @change="checkHeaderChange()"></el-checkbox>
    </template>
    <template slot="header">
      <div :style="getColumnEl_l_style(columnEl)">
        <span v-for="(l_s,l_i) in getColumnEl_l_s_t('l',columnEl)" :key="l_i">
          <span v-if="l_s!=='\n'">{{ lValue(l_s) }}</span>
          <br>
        </span>
      </div>
    </template>
    <template v-if="columnEl.s">
      <swcolumns v-for="(el,i) in columnEl.s" :key="levelIndex*1000+i" :level-index="intoNextLevel()" :header-checked="headerChecked" :type="type" :column-el="el" :column-index="i+columnIndex" :prop-set="propSet" :tree-props="treeProps"></swcolumns>
    </template>
    <template v-if="!columnEl.s" v-slot="scope">
      <span v-if="columnIndex===0 && !scope.row[treeProps.children]" :class="`${columnEl.align ? '' : ''}`"></span>
      <!-- order -->
      <template v-if="columnEl.order">
        <span>{{ scope.$index+1 }}</span>
      </template>
      <!-- e_n -->
      <template v-else-if="vif__C('e_n',columnEl,scope.row,scope.$index)">

        <span v-if="!propSet.canedit">{{ pValue(columnEl,scope.row,scope.$index) }}</span>
        <el-input
          v-else
          v-model="scope.row[`${columnEl.p}`]"
          size="mini"
          :disabled="!propSet.canedit"
          @change="e_n_valueEdit(columnEl,scope.row[columnEl.p], scope.row,scope.$index)"
          @input="getE_N_Limmit(columnEl,scope.row[columnEl.p], scope.row,scope.$index)"
        >
          <template v-if="columnEl.preStr" slot="prefix"> {{ columnEl.preStr }} </template>
          <template v-if="columnEl.edStr" slot="suffix">  {{ columnEl.edStr }} </template>
        </el-input>

      </template>
      <!-- e_t -->
      <template v-else-if="vif__C('e_t',columnEl,scope.row,scope.$index)">
        <span v-if="!propSet.canedit">{{ pValue(columnEl,scope.row,scope.$index) }}</span>
        <el-input
          v-else
          v-model="scope.row[`${columnEl.p}`]"
          size="mini"
          :disabled="!propSet.canedit"
          @change="e_t_valueEdit(columnEl,scope.row[columnEl.p], scope.row,scope.$index)"
          @input="e_t_valueEdit(columnEl,scope.row[columnEl.p], scope.row,scope.$index)"
        >
          <template v-if="columnEl.preStr" slot="prefix"> {{ columnEl.preStr }} </template>
          <template v-if="columnEl.edStr" slot="suffix">  {{ columnEl.edStr }} </template>
        </el-input>
      </template>
      <!-- e_date -->
      <template v-else-if="vif__C('e_date',columnEl,scope.row,scope.$index)">
        <span v-if="!propSet.canedit">{{ pValue(columnEl,scope.row,scope.$index) }}</span>
        <el-date-picker
          v-else
          v-model="scope.row[`${columnEl.p}`]"
          type="date"
          size="mini"
          :style="columnEl.e_style?columnEl.e_style : {width:'120px'}"
          :value-format="columnEl.valueFormat || ''"
          :format="columnEl.format || ''"
          placeholder=""
        ></el-date-picker>
      </template>
      <!-- link -->
      <template v-else-if="vif__C('link',columnEl,scope.row,scope.$index)">
        <el-link
          v-for="(el,i) in getForArr('link',columnEl)"
          :key="i"
          :style="{'margin-right':`${columnEl.gutter?columnEl.gutter:0}` + 'px'}"
          :disable="pip_transit('link_disable',el,scope.row, scope.$index) || false"
          :type="pip_transit('link_type',el,scope.row, scope.$index) || 'primary'"
          @click="linkAction(el,scope.row, scope.$index)"
        >{{ pip_transit('link_t',el,scope.row, scope.$index) || '' }}</el-link>
      </template>
      <!-- trans -->
      <template v-else-if="columnEl.trans">
        <span>{{ getTransText(columnEl,scope.row) }}</span>
      </template>
      <!-- check -->
      <template v-else-if="vif__C('check',columnEl,scope.row,scope.$index)">
        <el-checkbox v-model="scope.row[columnEl['checkedp']]" :disabled="getDisabled('disabled',columnEl)" @change="checkChange('checked', columnEl,scope.row, scope.$index)"></el-checkbox>
      </template>
      <!-- p -->
      <template v-else>
        <span :style="getStyle('style', columnEl, scope.row, scope.$index)">
          <template v-if="isArray(pValue(columnEl,scope.row,scope.$index))">
            <span v-for="(p_s,p_i) in transToArr(pValue(columnEl,scope.row,scope.$index))" :key="p_i">
              <span>{{ p_s }}</span>
              <br>
            </span>
          </template>
          <template v-else>
            <span>{{ pValue(columnEl,scope.row,scope.$index) }}</span>
          </template>
        </span>
      </template>

    </template>
  </el-table-column>
</template>

<script>
import bus from './js/bus'
import {
  getAlignBy_l
} from './js/formlist'
export default {
  name: 'Swcolumns',
  props: {
    columnEl: { // columnEloptions
      type: Object,
      default: () => {
        return {}
      }
    },
    columnIndex: { // column
      type: Number,
      default: 0
    },
    treeProps: { // 
      type: Object,
      default: () => {
        return {}
      }
    },
    propSet: { // 
      type: Object,
      default: () => {
        return {}
      }
    },
    type: { // el-table-column type selection
      type: String,
      default: ''
    },
    checkHeader: { // tablecheckbox
      type: Boolean,
      default: false
    },
    headerChecked: { // tablecheckbox
      type: Boolean,
      default: false
    },
    levelIndex: {//  
      type: Number,
      default: 0
    }

  },
  data() {
    return {
      colCheckHeader: this.headerChecked
    }
  },
  created() {
  },
  methods: {
    intoNextLevel() { //  +1 
      return Number(this.levelIndex) + 1
    },
    vif__C(type, el, row, index) { // v-if   'boolean''function'
      if (el[type] && typeof el[type] === 'boolean') {
        return el[type]
      } else if (el[type] && typeof el[type] === 'object') {
        return el[type]
      } else if (el[type] && typeof el[type] === 'function') {
        return el[type]({ el, row, index })
      } else {
        return false
      }
    },
    getE_N_Limmit(el, val, row, index) {
      if (el.point) { this.limitInput_PointNumber(el, val, row, index) } else { this.e_n_valueEdit(el, val, row, index) }
    },
    e_n_valueEdit(el, val, row, index) { // 'e_n'
      val = val.replace(/[^\d]/g, '')

      if (!val || val === '' || `${Number(val)}` === 'NaN') { row[`${el.p}`] = Number(0) } else { row[`${el.p}`] = Number(val) }
      if (el.eFunc) { return el.eFunc({ val, row, index }) }
      if (el.iFunc) { return el.iFunc({ val, row, index }) }
    },
    e_t_valueEdit(el, val, row, index) { // 'e_t'
      if (el.eFunc) { return el.eFunc({ val, row, index }) }
      if (el.iFunc) { return el.iFunc({ val, row, index }) }
    },
    limitInput_PointNumber(el, val, row) {
      //  
      if (row[el.p] === '.') { row[el.p] = '0.' } //   0
      row[el.p] = row[el.p].replace(/[^\d.]/g, '')
      row[el.p] = row[el.p].replace(/\.{2,}/g, '.')
      row[el.p] = row[el.p].replace('.', '$#$').replace(/\./g, '').replace('$#$', '.')
      var dd = ''
      var n = el.point && el.point !== true ? el.point : 2 // num 2(num = 2)
      for (let i = 0; i < n; i++) { dd = dd + '\\d' }
      row[el.p] = row[el.p].replace(new RegExp(`^(\\-)*(\\d+)\\.(${dd}).*$`), '$1$2.$3') //   num  (\d\d)
      if (row[el.p].indexOf('.') < 0 && row[el.p] !== '') {
        row[el.p] = parseFloat(row[el.p])
      }
    },
    getForArr(type, el) { //  el[type]'boolean' 
      // if ( typeof el[type] !== 'boolean') {
      // }
      if (el[type] && typeof el[type] === 'boolean') { return [el] } else { return el[type] }
    },
    getArrForValue(type, row) { //  el[type]'string' 
      if (row[type] && typeof row[type] === 'string') {
        return [row[type]]
      // eslint-disable-next-line valid-typeof
      } else {
        return row[type]
      }
    },
    isArray(val) { // 'val'
      if (val && typeof val === 'object') {
        return true
      } else {
        return false
      }
    },
    transToArr(val) { // 'val''string' 
      if (val && typeof val === 'string') {
        return [val]
      } else if (val && typeof val === 'object') {
        return val
      }
    },

    pip_transit(type, el, row, index) { //  
      if (el[type] && typeof el[type] === 'boolean') {
        return el[type]
      } else if (el[type] && typeof el[type] === 'string') {
        return el[type]
      } else if (el[type] && typeof el[type] === 'function') {
        return el[type]({ el, row, index })
      } else {
        return false
      }
    },
    linkAction(el, row, index) { // 'noti_bus_link'  '{ el, row, index }'
      bus.$emit('noti_bus_link', { el, row, index })
    },
    getHasChildrenClass() {

    },

    pValue(el, row, index) { //  p  String or Function
      if (el.p && typeof el.p === 'string') {
        return row[el.p]
      } else if (el.p && typeof el.p === 'function') {
        return el.p({ el, row, index })
      } else {
        return ''
      }
    },
    lValue(string) {
      return string
    },
    getColumnEl_l_s_t(type, el) { //  el[type]'string' 
      if (el[type] && typeof el[type] === 'string') { return [el[type]] } else { return el[type] }
    },
    getTransText(el, row) { //  'Int'
      return el.trans_map[row[el.p]]
    },
    getColumnEl_l_style(el) { //  
      if (el.l_style) { return el.l_style } else {
        // return { 'color': 'black', 'font-size': '14px', 'text-align': 'center' }
        return {}
}
    },

    getAlign(el) { //  
      // if (el.align) { return el.align } else { return el.s ? 'center' : 'left' }
      if (el.align) { return el.align } else {
        return el.s ? 'center' : getAlignBy_l(el.l)
      }
    },
    getWidth(el) { //  
      if (el.width) { return el.width } else { return '' }
    },
    getStyle(type, el, row, index) { // cssstyle
      return this.pip_transit(type, el, row, index)
    },
    getType(el) { // type
      if (el.type) { return el.type }
    },
    getDisabled(p, el) { //  check  true  falsefalse
      return el[p]
    },
    checkHeaderChange() { // checkbox
      bus.$emit('noti_bus_checkheader')
    },
    checkChange(p, columnEl, row, index) { // checkbox
      bus.$emit('noti_bus_check', { p, row, index })
    },
    getFixed(el) { // 
      if (el.fixed) {
        return true
      } else {
        return false
      }
    }

  }
}
</script>
<style scoped>
.noicon-blank{
  padding-left:25px
}

</style>
