<template>
  <div>
    <el-table
      ref="table"
      v-loading="loading"
      size="mini"
      :row-class-name="tableRowClassName"
      :header-cell-style="tableHeaderStyle"
      :row-style="rowStyle"
      :cell-style="cellStyle"
      :data="tableData || listdata"
      :style="tableStyle"
      :height="height"
      border
      :row-key="rowKey"
      :default-expand-all="defaultExpandAll"
      :tree-props="treeProps"
      tooltip-effect="dark"
    >
      <swcolumns v-for="(el, i) in options" :key="i" :header-checked="headerChecked" :type="el.type||''" :column-el="el" :column-index="i" :prop-set="propSet" :tree-props="treeProps"></swcolumns>
    </el-table>
  </div>
</template>
<script>
import bus from './js/bus'
import { getTableRowClass } from './js/formlist'
// swColumn    bus.js 
import swcolumns from './swColumn'
// ps props
const ps = {
  canedit: true, // 
  textSet: {} // 
}
export default {
  name:'vue-sw-formlist',
  components: {
    swcolumns
  },
  props: { //    'data' 'listdata' 'refs'
    tableData: {
      type: Array,
      default: () => {
        return null
      }
    },
    rowKey: { //  'rowKey''id'
      type: String,
      default: 'id'
    },
    treeProps: { //  'children'  'hasChildren'  ''
      type: Object,
      default: () => {
        return { children: '', hasChildren: '' }
      }
    },
    colortips: { // 
      type: String,
      default: null
    },
    options: { // 
      type: Array,
      default: () => {
        return []
      }
    },
    loading: { // 
      type: Boolean,
      default: false
    },
    propSet: { //  'ps'
      type: Object,
      default: () => {
        return {
          ...ps
        }
      }
    },
    defaultExpandAll: { // 'childData'
      type: Boolean,
      default: true
    },
    // eslint-disable-next-line vue/require-default-prop
    height: [String, Number],
    tableHeaderStyle: { // 
      type: Object,
      // default: () => { return { 'background-color': '#F5F7FA', 'border-color': 'gray' } }
      default: () => { return {} }
    },
    tableStyle: { // 
      type: Object,
      // default: () => { return { 'border-color': 'gray', 'border-left': '1px solid gray', 'border-right': '1px solid gray', 'border-top': '1px solid gray', 'border-bottom': '1px solid gray', 'width': '100%' } }
      default: () => { return {} }
    },
    rowStyle: { // 'row'
      type: Object,
      default: () => { return {} }
    },
    cellStyle: { // 'cell'
      type: Object,
      // default: () => { return { 'border-color': 'gray' } }
      default: () => { return { } }
    }

  },
  data() {
    return {
      listdata: [],
      headerChecked: false
    }
  },
  watch: {
    'tableData.length': {
      handler() {
        this.$nextTick(() => {
          this.$refs.table.doLayout()
        })
      }
    },
    'listdata': {
      handler() {
        this.$nextTick(() => {
          this.$refs.table.doLayout()
        })
      }
    }
  },
  mounted() {
    this.bindBusNoti() // 
  },
  beforeDestroy() { // 

    /* ********************
     * *************
     * ********************
     *
     * formlist  formlist 
     * formlist 'link_noti'
     * */

    // bus.$off('noti_bus_link')
  },
  methods: {
    bindBusNoti() { //  ('mounted')
      bus.$on('noti_bus_link', ({ el, row, index }) => { // link 
        this.$emit(el.link_noti, { el, row, index })
      })
      bus.$on('noti_bus_checkheader', () => { // checkbox
        this.headerChecked = !this.headerChecked
        this.listdata.forEach((el, index) => {
          el.checked = !!(this.headerChecked)
          // if (el[this.treeProps.children]) {
          //   el[this.treeProps.children].forEach((item, i) => {
          //     item.checked = !!this.headerChecked
          //   })
          // }
          this.$set(this.listdata, index, el)
        })
        // this.$emit('noti_check_header')
      })
      bus.$on('noti_bus_check', ({  row,  }) => { // checkbox
        // row.checked = !row.checked
        if (this.listdata && this.listdata.length > 0) {
          this.listdata.forEach((el, i) => {
            if (el[this.rowKey] && el[this.rowKey] === row[this.rowKey]) {
              this.$set(this.listdata, i, row)
            }
          })
        }
      })
    },
    tableRowClassName({ row, rowIndex }) { // 'row'
      return getTableRowClass(row, rowIndex, this.colortips)
    }

  }
}
</script>
